.new {
  width: 100%;
  display: flex;

  .newContainer {
    flex: 6;

    .top,
    .bottom {
      box-shadow: 2px 2px 2px 1px rgba(201, 201, 201, 0.47);
      padding: 10px;
      margin: 20px;
      display: flex;
      align-items: center;

      h1 {
        color: lightgray;
        font-size: 20px;
      }

      .left {
        flex: 1;
        text-align: center;

        img {
          width: 300px;
          height: 300px;
          border-radius: 10%;
          object-fit: cover;
        }
      }

      .right {
        flex: 2;
        display: flex;
        flex-direction: column;
        align-items: center;

        .productForm {
          display: flex;
          flex-wrap: wrap;
          gap: 30px;
          padding: 10px;
          justify-content: space-between;
          
          .formInput {
          width: 40%;
            
         
            select{
              border: none;
            }

            label {
              display: flex;
              align-items: center;
              gap: 10px;

              .icon {
                cursor: pointer;
              }
            }

            input {
              width: 100%;
              padding: 5px;
              border: none;
              border-bottom: 1px solid gray;
            }
          }
          .formInput:last-child{
            width: 100%;
          }
        }
    
     
           button.addUser{
            
            width: 150px;
            padding: 10px;
            border: none;
            background-color: #3E1B11;
            color: white;
            font-weight: bold;
            cursor: pointer;
            margin: 30px;
          }
          button.addAutomat{
            
            width: 150px;
            padding: 10px;
            border: none;
            background-color: #FCC95F;
            color: white;
            font-weight: bold;
            cursor: pointer;
            margin: 30px;
          }
          button.cancel{
            
            width: 150px;
            padding: 10px;
            border: none;
            background-color: red;
            color: white;
            font-weight: bold;
            cursor: pointer;
            margin: 30px;
          }
          
      }
    }


  }
}
