.app{
  height: 100vh;
}
  .app.dark {
  background-color: #111;
  color: rgb(156, 156, 156);

  .chartGrid {
    stroke: rgba(228, 228, 228, 0.219);
  }

  .table {
    background-color: #111;

    .tableCell {
      color: gray;
    }
  }

  .navbar {
    color: #999;
    border-color: #333;

    .search {
      border-color: gray;
    }
  }

  .sidebar {
    background-color: #111;
    border-color: #333;

    .top {
      border-color: #333;
      .menuIcon{
        background-color: #111;
        color: white;
      }
      .logo {
        color: #999;
      }
    }
 

    ul {
      li {
        &:hover {
          background-color: #333;
        }

        .icon {
          color: #999;
        }
      }
    }
  }
  .newContainer{
    background-color: #111;
    border-color: #333;
  }
  .profile .profileContainer .resetButton {
    color: white;
    border:1px solid white;
  }


  .navbar{
    .logo{
      color: white;
    }
  }
  .datatable {
    .datagrid {
      color: gray;
      border: none;

      .viewButton,
      .deleteButton,
      .editButton,
      .cellWithStatus {
        color: gray;
        border: none;
      }
    }
  }
  .widgets{
    span{
      color: gray;
    }
  }
  input{
      background-color: transparent;
  }
}

